<template>
  <div id="productDetail">
    <Header/>
    <div class="product-detail-main clearFloat container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{path: '/productList'}">产品列表</el-breadcrumb-item>
        <el-breadcrumb-item>产品详情</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="product-detail-wrap clearFloat">
        <div class="product-detail-img fl" v-if="type === 'pc'">
          <div class="pic-zoom">
            <pic-zoom :url="url" :scale="3" :big-url="url" :scroll="true"></pic-zoom>
          </div>
          <div class="small-img clearFloat">
            <i class="el-icon-arrow-left arrow-left fl" @click="imgScroll('left')"></i>
            <div class="small-img-wrap fl">
              <ul class="scroll-wrap">
                <li v-for="item in urlList">
                  <img :src="item.smallUrl" :data-big="item.bigUrl" alt="" @mousemove="mouseover(item.bigUrl)" :class="{srcActive:srcActive==item}">
                </li>
              </ul>
            </div>
            <i class="el-icon-arrow-right arrow-right fr" @click="imgScroll('right')"></i>
          </div>
        </div>
        <div class="phone-banner" v-else-if="type === 'phone'">
          <wc-swiper>
            <wc-slide v-for="item in urlList">
              <img :src="item.bigUrl" alt="">
            </wc-slide>
          </wc-swiper>
        </div>
        <div class="product-detail">
          <h3 class="product-detail-title">{{paramsName}}</h3>
          <div class="product-detail-price mgb20 division">
            <!--<span class="price-text"></span>-->
            <b><span class="price">{{paramsPrice}}</span></b>
          </div>
          <div class="address">
            <span class="same-title">配送至：</span>
            <span class="address-text" @click="addressWrap = !addressWrap" v-if="address_default.address">{{address_default.address_name}}</span>
            <!--<span class="address-text" @click="addressWrap = !addressWrap" v-else>{{this.currentCity}}</span>-->
            <span class="address-text" @click="addressWrap = !addressWrap" v-else>
              <el-cascader
                size="mini"
                expand-trigger="hover"
                :options="options"
                v-model="selectedOptions2"
                @change="handleChange">
              </el-cascader>
            </span>
            <span class="same-title" v-show="address_default.address"> > 有货 </span>
            <div class="address_warp" v-show="addressWrap">
              <div class="address_dialog">
                <b class="address_close el-icon-close" @click="addressWrap = !addressWrap"></b>
                <ul class="address_lists clearFloat">
                  <li v-for="addressItem in addressList" class="address_item" @click="addressSelect(addressItem.address_name)" :class="{active : addressActive == addressItem.address_name}">
                    <!--<div class="address_default">默认地址</div>-->
                    <p class="address_detail">{{addressItem.address_name}}</p>
                    <p>{{addressItem.user_phone}}</p>
                    <p>{{addressItem.user_name}}（收）</p>
                    <i>已选中</i>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div class="mgb20">
            <span class="same-title">重量：</span>
            <span class="same-title">2KG</span>
          </div>
          <div class="select-parameter mgb20 clearFloat">
            <span class="same-title fl sel-color">
              COLOR：{{this.paramsColor}}
            </span>
            <ul class="clearFloat">
              <li v-for="item in wpList" :key="item.name" :class="{active : paramsColor == item.name}" @click="selected('parameter',item.name)">{{item.name}}</li>
            </ul>
          </div>
          <div class="select-parameter mgb20 clearFloat">
            <span class="same-title fl sel-color">
              SIZE：{{this.paramsSize}}
            </span>
            <ul class="clearFloat">
              <li v-for="item in szList" :key="item.name" :class="{active : paramsSize == item.name}" @click="selected('size',item.name)">{{item.name}}</li>
            </ul>
          </div>
          <div class="product-detail-price mgb20">
            <b><span class="price">¥45.00</span></b>
          </div>
          <div class="mgb20">
            <div class="input-number">
              <el-input-number v-model="num" controls-position="right" :min="1" :max="10"></el-input-number>
              <button class="cat-btn" @click="addToCart">加入购物车</button>
              <i class="iconfont icon-third-heart"></i>
            </div>
          </div>
          <div class="btn-wrap">
            <button class="buy-btn">立即购买</button>
          </div>
        </div>
        <div class="active-info">
          <div class="coupon active-list" v-for="(index,dom) in 2">
            <h2>可用优惠券</h2>
            <div class="coupon-info" v-for="(index,dom) in 2">
              <img src="../../common/img/u403.png"/>
              <div class="coupon-content">
                <p>全品类服装9.8折</p>
                <span>不与同类优惠叠加</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Map v-show="false"/> <!--该组件要引用才能获取定为-->
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import PicZoom from 'vue-piczoom'
  import {mapState} from 'vuex'
  import Map from '../style2/Map'
  import Header from '../style2/Header'
  import options from '../../common/js/addressJson'
  export default {
    components: {
      PicZoom,
      Map,
      Header,
    },
    data() {
      return {
        selectedOptions2: ['zujian', 'data', 'tag'],
        options,
          goodsInfo:{   //添加到购物车的商品信息
          goodsName:'',
          goodsNum:'',
          goodsPic:'',
          goodsPrice:'',
          goodsColor:'',
          goodsSize:'',
          state:Boolean
        },
        paramsPrice:'¥45.00-￥48.00',
        paramsName:'0-24M Baby Girl Turn-Down领长袖蕾丝上衣',
        paramsColor:'红色',
        paramsSize:'1',
        addressActive:'',
        active1:'商品介绍',
        srcActive :'',
        num: 1,
        tempLength : 0,//临时变量,当前移动的长度
        type: '',
        addressWrap:false,
        postdata:"",
        configs: {
          width:400,
          height:400,
          maskWidth:100,
          maskHeight:100,
          maskColor:'#ddd',
          maskOpacity:0.5
        },
        address_default:{address_name:'',address:false},
        addressList:[{address_name:'广东省深圳市龙华新区民治街道向南新村32栋',user_name:'朱俊业',user_phone:'18512600311'},{address_name:'广东省深圳市龙华新区民治街道向南新村40栋',user_name:'张宗师',user_phone:'18512600311'}],
        introduceList:[{name:'商品介绍'},{name:'商品评价（100）'}],
        wpList: [
          {name: '红色'},
          {name: '绿色'},
          {name: '黑色'},
          {name: '紫色'},
          {name: '卡其色'}
        ],
        szList: [
          {name: '1'},
          {name: '2'},
          {name: '3'},
          {name: '4'},
          {name: '5'},
          {name: '6'}
        ],
        url:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',
        urlList:[
          {smallUrl:'http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg',bigUrl:'http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg'},
          {smallUrl:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',bigUrl:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg'}
        ]
      }
    },
    created: function(){
      this.postdata=this.$route.params.zh_pa_id;
      console.log(this.postdata);
    },
    mounted (){
      this.screenWidth();
      console.log(this.options)
    },
    methods: {
      mouseover: function (imgSrc) {
        this.url = imgSrc;
        this.srcActive = imgSrc
      },
      screenWidth: function() {
        if(document.body.clientWidth <= 767){
          this.type = 'phone'
          console.log(this.type)
        }else {
          this.type = 'pc'
        }
      },
      selected(type,name){
        if (type == "parameter") {
          this.paramsColor = name;
        }
        else if (type == "size") {
          this.paramsSize = name;
        }
        else {
          this.paramsSize = name;
        }
      },
      imgScroll: function (type) {
        let viewNum = 5; //设置每次显示图片的个数量
        let moveNum = 2; //每次移动的数量
        let scrollDiv = document.querySelector(".scroll-wrap"); //进行移动动画的容器
        let scrollItems = document.querySelectorAll(".small-img-wrap ul li"); //移动容器里的集合
        let moveLength = 87 * moveNum; //计算每次移动的长度
        let countLength = (scrollItems.length - viewNum) * 87; //计算隐藏li的长度
        let moveLeft = Number(window.getComputedStyle(scrollDiv).left.slice(0,-2));//容器的left值
        if(type == 'right'){
          if(this.tempLength < countLength){
            if((countLength - this.tempLength) > moveLength){
              scrollDiv.style.left = `${moveLeft - moveLength}px`;
              this.tempLength += moveLength;
            }else{
              scrollDiv.style.left = `${moveLeft - (countLength - this.tempLength)}px`;
              this.tempLength = countLength;
            }
          }
        }else {
          if(this.tempLength > 0){
            if(this.tempLength > moveLength){
              scrollDiv.style.left = `${moveLength + moveLeft}px`;
              this.tempLength -= moveLength;
            }else{
              scrollDiv.style.left = `${this.tempLength + moveLeft}px`;
              this.tempLength = 0;
            }
          }
        }
      },
      addressSelect: function (addressName) {
        this.address_default.address_name = addressName;
        this.address_default.address = true;
        this.addressActive = addressName;
        this.addressWrap  = false
      },
      addToCart(){
        this.goodsInfo.goodsName = this.paramsName  //商品名
        this.goodsInfo.goodsNum = this.num          //数量
        this.goodsInfo.goodsPrice= this.paramsPrice   //价格
        this.goodsInfo.goodsPic = this.urlList[0].smallUrl  //图片
        this.goodsInfo.goodsColor = this.paramsColor    //颜色
        this.goodsInfo.goodsSize = this.paramsSize      //尺寸
        this.goodsInfo.state = true  //是否有货
        this.$store.dispatch("receive_goodsInfo",{goodsInfo:this.goodsInfo})
      },
      handleChange(value) {
        this.num = value
      }
    },
    computed:{
      ...mapState(['currentCity'])
    }
  };
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "../../common/stylus/general.styl"
#productDetail
  position relative
  .product-detail-main
    margin-top 20px
    margin-bottom 20px
  .product-detail-wrap
    margin-top 20px
    position relative
  .magnifier-box img
    height 100%
  .pic-zoom
    width 400px
    height 400px
    border 1px solid #ddd
  .small-img
    width 400px
    position relative
    margin-top 15px
  .small-img-wrap
    width 340px
    overflow hidden
    ul
      width 10000px
      overflow hidden
      position relative
      left 0
      transition all .3s ease-in
      li
        float left
        margin-right 10px
        img
          width 77px
          height 77px
          cursor pointer
          box-sizing border-box
        .srcActive
          border 1px solid color
  .arrow-left,.arrow-right
    display block
    font-size 30px
    color color2
    height 80px
    line-height 80px
    cursor pointer
  .product-detail
    transition all .2s ease-in
    display inline-block
    width 40%
    margin-left 15px
  .product-detail-title
    color #333
    font-size 16px
    font-weight 900
    margin 10px 0
  .division
    padding-bottom 5px
    border-bottom 1px solid #eee
  .product-detail-price
    border-radius 2px
    font-size 16px
  .price
    font-wight:900
  .address
    height 30px
    line-height 30px
    margin 10px 0px
  .address-text
    color color2
    font-size 14px
    cursor pointer
    display inline-block
    max-width 62%
    vertical-align bottom
    overflow hidden
    text-overflow ellipsis
    white-space nowrap
  .address_warp
    position absolute
    z-index 1000
    top 32px
    left 0
  .address_dialog
    width 488px
    position relative
    background-color #fff
    border 1px solid #aaa
    padding 10px
  .address_close
    position absolute
    right 5px
    cursor pointer
    overflow hidden
    font-size 18px
    top 3px
    color #333
  .address_lists
    height 204px
    overflow auto
    margin-right -10px
  .address_item
    width 215px
    height 82px
    line-height 18px
    margin 10px 10px 0px 0 !important
    display block
    float left
    cursor pointer
    position relative
    overflow hidden
    padding 5px 8px
    font-size 14px
    color #333
    background-image url(../../common/img/select-unti.png)
    background-repeat no-repeat
    background-position: 0 0
    background-size 100% 100%
  .address_item:hover
    background-image url(../../common/img/addree-select.png)
  .address_item:nth-child(2n)
    margin-right 0 !important
  .address_default
    position absolute
    right 0
    top 0
    background-color #ccc
    color #FFF
    padding 0 2px
    opacity .7
    font-size 12px
    line-height 1.5
  .address_item.active
    background-image url(../../common/img/addree-select.png)
  .address_item.active i
    display block
  .address_dialog i
    background-image url(../../common/img/select-right.png)
    position absolute !important
    bottom 0
    right 0
    width 12px
    height 12px
    overflow hidden
    text-indent -99em
    display none
    background-repeat no-repeat
    background-position 0 0
  .address_detail
    line-height 20px
    height 40px
    display -webkit-box
    -webkit-line-clamp 2
    -webkit-box-orient vertical
    overflow hidden
    text-overflow ellipsis
  .same-title
    color #666
    display inline-block
    text-align left
  .sel-color
    display block
    font-size 14px
    font-weight 900
    width 100%
  .select-parameter li
    margin-right 2%
    padding 5px 10px
    margin-top 10px
    font-size 14px
    color #666
    text-align center
    cursor pointer
    border-radius 5px
    display inline-block
  .select-parameter ul
    width 68%
    margin 0
  .clearFloat:after
    clear:none
  .select-parameter li.active
    color color2
    border 1px solid color2
  .input-number>>>.el-input-number
    width 100px
  .buy-btn,.cat-btn
    padding 0 10px
    font-size fs
    border none
    outline none
    height 40px
    cursor pointer
    border-radius 2px
  .buy-btn
    background color2
    color #fff
    width 250px
    font-size fl
  .cat-btn
    background: #b59677;
    color: #fff;
    margin-left 10px
  .icon-third-heart
    font-size 25px
    color color2
    margin-left 5px
    cursor pointer
  .introduce-item
    margin-top 40px
  .active-info
    display inline-block
    width 23%
    vertical-align top
    margin-left 10px
    .coupon
      margin-bottom 50px
      h2
        height 40px
        line-height 40px
        border-bottom 1px solid
        font-weight 600
        color #999999
      .coupon-info
        display flex
        align-items center
        height 80px
        background #eee
        margin-top 20px
        border 1px solid #ddd
        padding 10px
        img
          flex 3
          width 70px
          height 70px
        .coupon-content
          flex 7
          padding 10px
          p
            height 50px
            font-size fm
          span
            font-size fs
            height 20px
            line-height 20px
  /* 小于767px */
  @media screen and (max-width: 767px){
    .product-detail-main{
      padding-bottom 1.3rem
    }
    .container{
      width 95%
      padding 10px
    }
    .product-detail{
      float none
      display inline-block
      width 100%
    }
    .active-info{
      width 95%
    }
    .product-detail-price{
      padding-left 0
    }
    .phone-banner img{
      width 100%
    }
    .product-detail-img{
      display none
    }
    .address_dialog{
      width 100%
      box-sizing border-box
    }
    .address_close{
      right 0
    }
    .address_item{
      width 42%
    }
  }
</style>



