import Vue from 'vue'
import {RECEIVE_CURRENTCITY,RECEIVE_GOODSINFO} from './mutations-type'
export default{
  [RECEIVE_CURRENTCITY](state,{currentCity}){
    state.currentCity = currentCity
  },
  [RECEIVE_GOODSINFO](state,{goodsInfo}){
    if(goodsInfo.goodsNum){
      goodsInfo.goodsNum ++
    }
    else {
      state.goodsInfo.push(goodsInfo)
    }
  }
}



