<template>
    <div class="user-center">
      <div class="nav">
        <span class="iconfont icon-third-mine"></span>
        <span class="iconfont icon-third-heart"></span>
        <span class="iconfont icon-third-shoppingcart" @click="myShopCart"></span>
      </div>
      <transition name="fade" enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutRightBig">
        <div class="shop-cart-list" v-show="isOpen">
          <p class="title">MY CART</p>
          <div class="product-list">
            <div class="goods-list">
              <div class="product-item" v-for="(index,item) in goodsInfo" :key="index">
                <img :src=goodsInfo[item].goodsPic />
                <div class="param">
                  <p class="name">{{goodsInfo[item].goodsName}}</p>
                  <p class="color">color：{{goodsInfo[item].goodsColor}}</p>
                  <p class="size">鞋号：{{goodsInfo[item].goodsSize}}</p>
                  <p class="price">￥{{goodsInfo[item].goodsPrice}}</p>
                  <p>
                    <template>
                      <el-input-number size="mini" v-model="num1" @change="handleChange" :min="1" :max="10"
                                       label="描述文字"></el-input-number>
                    </template>
                  </p>
                </div>
                <span class="iconfont icon-third-plus" style="margin-left:20px"></span>
              </div>
            </div>
          </div>
          <div class="describe">
            <p class="tips">买家特别说明：</p>
            <textarea></textarea>
            <p class="money">小计:<span class="price">￥666.00</span></p>
            <el-progress :percentage="100" color="#b59677"></el-progress>
            <button class="postage btn">°ω° 恭喜！你有免费送货</button>
            <p><el-checkbox></el-checkbox><a>条款和条件</a></p>
            <button class="agreement btn">查看</button>
            <button class="buy btn">立即购买</button>
          </div>
        </div>
      </transition>
    </div>
</template>

<script>
  import {mapState} from 'vuex'
    export default {
      name: "MyInformation",
      props:{
        isOpen:{
          default:false
        }
      },
      data(){
          return{
            num1: 1
          }
      },
      methods:{
        myShopCart(){
          this.isOpen = !this.isOpen
          this.$emit('ShowModal', this.isOpen);
          console.log(this.goodsInfo)
        },
        handleChange(value) {
          console.log(value);
        }
      },
      computed:{
        ...mapState(['goodsInfo'])
      }
    }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
@import "../../common/stylus/general.styl"
  .user-center
    width 15%
    float right
    .nav
      display flex
      span
        flex 1
        text-align center
        font-size fxxl
    .shop-cart-list
      z-index 100
      background #fff
      height max-content
      width 100%
      position absolute
      margin-top 10px
    .title
        height 40px
        line-height 40px
        background #000
        color #fff
        font-weight 600
        text-align center
      .goods-list
        height 300px
        overflow auto
        border-bottom 1px solid #c6c6c6
        .product-item
          display flex
          img
            flex 2
            width 70px
            height 70px
            margin 20px 10px 10px 10px
            cursor pointer
          .param
            flex 7
            font-size 14px
            margin-top 20px
            p
              line-height 25px
            .name
              font-weight 400
              cursor pointer
            .name:hover
              color color2
              transition 0.5s
            .color,.size
              color #999
            .price
              color color2
          .delete
            flex 1
      .describe
        padding 20px
        p
          height 25px
          line-height 25px
        .tips
          height 21px
          line-height 20px
          color #999
        textarea
          height 80px
          width 100%
        .money
          font-weight 700
          font-size fl
          margin-top -10px
          margin-bottom 20px
        .price
          font-weight 400
          float right
          color color2
        .btn
          height 40px
          line-height 40px
          display block
          width 100%
          border none
          margin 10px 0
          color #fff
          border-radius 5px
          letter-spacing 2px
          cursor pointer
        a
          margin-left 10px
          cursor pointer
        a:hover
          color color2
        .postage
          background #ff4b4b
        .agreement
          background #596565
        .buy
          background color2
/* 小于767px */
@media screen and (max-width: 768px){
  .user-center{
    width 50%
  }
}
</style>



