import {RECEIVE_CURRENTCITY,RECEIVE_GOODSINFO} from './mutations-type'
export default{
  receive_currentCity({commit},{currentCity}){
    commit(RECEIVE_CURRENTCITY,{currentCity})
  },
  receive_goodsInfo({commit},{goodsInfo}){
    commit(RECEIVE_GOODSINFO,{goodsInfo})
  }
}



