<template>
  <div id="footer">
    <div id="mall-footer" class="hideBox">
      <div class="mall-footer-main container clearFloat">
        <div class="clearFloat">
          <div class="foot-logo">
            <div class="logo-share">
              <router-link to="/index_1">
                <img src="../../common/img/abckis.png" alt="" width="200px">
              </router-link>
            </div>
            <p class="support">
              support@abc-kids.net
            </p>
            <ul class="share-list clearFloat">
              <li v-for="item in shareList">
                <router-link :to="item.url">
                  <img :src="item.icon" alt="" width="20px">
                </router-link>
              </li>
            </ul>
          </div>
          <div class="foot-text">
            <div class="foot-text-list" v-for="item in 3">
              <span>Customer Care</span>
              <ul>
                <li>
                  <router-link to="1">
                    FAQs
                  </router-link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="footer__bot">
          <div>
            <p> &copy;Copyright 2019 <span>ABC KIDS</span> all rights reserved.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        shareList:[
          {url:'1',icon:'FB.png'},
          {url:'2',icon:'ins.png'},
          {url:'3',icon:'twitter.png'}
        ]
      }
    },
    mounted (){
      for (let i = 0;i<this.shareList.length; i++){
        this.shareList[i].icon = require('../../common/img/'+this.shareList[i].icon)
      }
    },
    methods: {

    }
  };
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../../common/stylus/general.styl"
  #footer
    position relative
    color #fff
    &:before
      content ''
      position absolute !important
      left 0
      top 0
      width 100%
      height 100%
      background rgba(62, 62, 62, 0.9)
  #mall-footer
    background url(../../common/img/footbg.jpg) no-repeat center center
    background-size cover
    padding-top 50px
  .foot-logo
    width 30%
    margin-bottom 15px
    float left
    padding 0 15px
    box-sizing border-box
  .support
    font-size fm
  .share-list
    margin-top 15px
    li
      float left
      margin-right 15px
  .footer__bot
    text-align center
    font-size 14px
    padding 20px 0
    span
      color color2
  .foot-text
    float left
    width 70%
  .foot-text-list
    float left
    width 33.333%
    padding 0 15px
    box-sizing border-box
    color #fff
    span
      display block
      font-size fm
      font-weight bold
      margin-bottom 15px
    ul
      li
        line-height 30px
        margin-bottom 5px
        a
          color #fff
          font-size 14px
  @media screen and (max-width: 767px){
    .foot-text{
      display none
    }
    .foot-logo{
      width 100%
      text-align center
    }
    .share-list{
      text-align center
      display inline-block
    }
    .mall-footer-main{
      position relative
    }
    #mall-footer{
      padding-top 0
    }
  }
</style>



