<template>
  <div class="amap-page-container">
    <div>
      <el-amap vid="amap" :plugin="plugin" class="amap-demo" :center="center">
      </el-amap>
    </div>


    <div class="toolbar">
      <span v-if="loaded">{{this.city}}</span>
      <span v-else>正在定位</span>
    </div>
  </div>
</template>

<style>
  .amap-demo {
    height: 300px;
  }
</style>

<script>
  module.exports = {
    data() {
      let self = this;
      return {
        center: [121.59996, 31.197646],
        city:'',
        lng: 0,
        lat: 0,
        loaded: false,
        plugin: [{
          pName: 'Geolocation',
          events: {
            init(o) {

              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  self.$nextTick(function () {
                    self.city= result.formattedAddress
                    this.$store.dispatch("receive_currentCity",{currentCity:this.city})
                  });
                }
              });
            }
          }
        }]
      };
    }
  };
</script>
<style>
  .amap-demo {
    height: 300px;
  }
</style>



