<template>
  <div>
    <div class="header-top">
      <div id="logo">
        <img src="../../common/img/logo.png">
      </div>
      <UserCenter @ShowModal="headCall" :isOpen="showModal"/>
    </div>
    <div class="modal" v-show="showModal" @click="closeModal"></div>
  </div>
</template>

<script>
    import UserCenter from "../style2/userCenter";
    export default {
        name: "Header",
      components: {UserCenter},
      data(){
          return{
            showModal: false  //是否显示模态窗
        }
      },
      methods:{
        headCall(msg){   //接收子组件传的值
          this.showModal = msg
        },
        closeModal(){   //点击模态框
          this.showModal = !this.showModal
        }
      }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/general.styl"
  .header-top
    padding 10px 0
    height 80px
    line-height 80px
    box-shadow 2px 1px 10px #c6c6c6
    #logo
      display inline-block
  .modal
    width 100%
    height 100%
    position absolute
    z-index 99
    background #000
    opacity:0.8
</style>



