// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import store from './store'
import AMap from 'vue-amap';
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import wcSwiper from 'wc-swiper'
import animate from 'animate.css'
import 'wc-swiper/style.css'
import 'swiper/dist/css/swiper.css'
import {post,fetch} from './utils/http'
import {setCookie,getCookie,delCookie} from './utils/cookie'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'jquery'
Vue.use(AMap);

// 初始化vue-amap
AMap.initAMapApiLoader({
  // 高德key
  key: '4d9800c74f2673aa9fe6a520cc7f109f',
  // 插件集合 （插件按需引入）
  plugin: ['AMap.Geolocation']
});

// axios.defaults.withCredentials=true;//让ajax携带cookie
//定义全局变量
window.axios = axios;
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
//Vue.prototype.$patch=patch;
//Vue.prototype.$put=put;
Vue.prototype.$setCookie = setCookie;
Vue.prototype.$getCookie = getCookie;
Vue.prototype.$delCookie = delCookie;
Vue.use(ElementUI);
Vue.use(wcSwiper);
Vue.use(VueAwesomeSwiper);
// router.push('index_1');
Vue.config.productionTip = false;


/* eslint-disable no-new */
//全局过滤器
Vue.filter('money',function(v){
  return `￥${v.toFixed(2)}`;
});
//注册全局指令
Vue.directive('focus',function (el) {
  el.focus()
});
//全局路由钩子动态加载title以及登录判断
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  //获取用户登录成功后储存的登录标志
  let userID = Vue.prototype.$getCookie("userId");
  if (to.meta.isLogin){
    if (userID){
      next()
    }else {
      Vue.prototype.$message.error('请先登录！');
      Vue.prototype.$setCookie('url',from.path);
      next({
        path: '/login',
      })
    }
  }else {
    next()
  }
//如果登录了禁止再进入登录路由,如果有来源就返回到来源处，如果没有就返回首页
  if (userID){
    if (to.path == '/login' || to.path == '/findPassword'){
      Vue.prototype.$message.success('您已登录~');
      if (from.path == "/"){
        next({
          path:'/index_1'
        })
      }else {
        next({path:from.path});
        location.reload()
      }
    }
  }else if(to.path == '/login' || to.path == '/findPassword'){
    if (from.path == "/") {
      Vue.prototype.$setCookie('url','/index_1');
      next();
    }
  }
});
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});



